import React from 'react'
import { Menubars, Navmenu ,Logo,Nav,Navmenulinks, NavBtn} from './navbarelements'
import { Menudata } from '../../data/Menudata'
import { Btn } from '../Btn'

const Navbar = ({toggle}) => {
    return (
        <Nav>
            <Logo>SLIDE</Logo>
            <Menubars onClick={toggle}/>
    <Navmenu>{Menudata.map((i,index)=>(
        <Navmenulinks to={i.link} key={index}>{i.title}</Navmenulinks>
    ))}</Navmenu>
           
           <NavBtn>
               <Btn to='/contact' primary='true'>Contact</Btn>
           </NavBtn>




        </Nav>
            

    )
}

export default Navbar
