import React from 'react'
import { Section, Container ,ColumnLeft,ColumnRight} from './infoelements'
import { Btn } from '../Btn'

const InfoSect = ({heading,paragraphOne,paragraphTwo,btnlabel,image,reverse}) => {
 return(
        <Section>

            <Container>

                <ColumnLeft>

                <h1>{heading}</h1>
                <p>{paragraphOne}</p>
                <p>{paragraphTwo}</p>
                <Btn to ="/homes" primary='true'>{btnlabel} </Btn>
                </ColumnLeft>


                <ColumnRight reverse={reverse}>

                <img src={image} alt='home'/>
                </ColumnRight>


                </Container>




        </Section>
 )    
    
}

export default InfoSect
