import React from 'react'
import { DropdownContainer,Icon,CloseIcon,DropdownMenu,DropdownWrapper,BtnWrap,DropdownLinks } from './dropdownelemetns'
import { Menudata } from '../../data/Menudata'
import { Btn } from '../Btn'

const Dropdown = ({toggle,isOpen}) => {
    return (
        <DropdownContainer isOpen={isOpen}>
            <Icon onClick={toggle}>
                <CloseIcon/>
            </Icon>
            <DropdownWrapper>

                <DropdownMenu>

                    {Menudata.map((i,index)=>(
                        <DropdownLinks to = {i.link} key={index}>
                            {i.title}
                        </DropdownLinks>

                    ))}

               

                </DropdownMenu>

                <BtnWrap>
                        <Btn primary = 'true' round='true' big='true' to="/contact">
                            Contact Us
                        </Btn>
                    </BtnWrap>



            </DropdownWrapper>




            </DropdownContainer>
            
       
    )
}

export default Dropdown
