import React from 'react'
import { HeroSection, HeroWrapper,HeroContent,HeroImage,Arrow,HeroSlide,HeroSlider,SliderButtons,PrevArrow,ForwardArrow} from './heroelements'
import { Btn } from '../Btn'

import styled , {css} from 'styled-components/macro'
import {useState,useRef,useEffect} from 'react'
const Hero = ({slides}) => {

const [ current,setCurrent] = useState(0)

const length= slides.length

const timeout = useRef(null)









const nextSlide=()=>{
    setCurrent(current === length-1 ? 0 : current + 1)
}


const prevSlide=()=>{
    setCurrent(current === 0 ? length-1 : current - 1)
}

if(!Array.isArray(slides) || slides.length<=0){
    return null
}

    return (
        <HeroSection>
            <HeroWrapper>

                {slides.map((i,index)=>{
                    return(
                        <HeroSlide key={index}>

                            {index === current && (

                            <HeroSlider>

                                    <HeroImage src={i.image} alt={i.alt}/>

                                            <HeroContent>

                                     <h1><strong>{i.title}</strong></h1>
                                        <p>{i.price}</p>
                                    <Btn to={i.path} primary='true' css={`max-width:160px;`}>
                                         {i.label}
                                        <Arrow/>
                                            </Btn>


                                        </HeroContent>

                                                </HeroSlider>

                            )}
                             



                        </HeroSlide>




                    )

                })}
                <SliderButtons>
                    <PrevArrow onClick={prevSlide}/>
                    <ForwardArrow onClick={nextSlide}/>
                </SliderButtons>

           

            </HeroWrapper>
       
            
        </HeroSection>
    )
}

export default Hero
